.success {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  text-align: center;
  z-index: 99999;
  gap: 3rem;
  background-color: rgba(57, 62, 70, 0.9);
  backdrop-filter: blur(5px);
}

.heading {
  color: var(--white);
  font-size: 1.5rem;
}

.animation {
  width: 15rem;
  height: 15rem;
}
